import React, { useEffect, useState } from "react";
import "./Sidebar.css";

import { jwtDecode } from "jwt-decode";
import { Link, useLocation, useNavigate } from "react-router-dom";
function Sidebar() {

  const [activeTab, setActiveTab] = useState("Bo Co Ca Hng");


  const handleClick = (label) => {
    setActiveTab(label);
    console.log(label);
  };

  const sidebars = []

  var decodedToken = ""

  var name = "";
  var token = "";
  var role = "";

  try {
    token = localStorage.getItem('token');
    decodedToken = jwtDecode(token);
    name = decodedToken['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name'];
    role = decodedToken['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/nameidentifier'];
  } catch (e) {
  }

  return (
    <div>
      <div className="viewport col-2">
        <div className="sidebar col-2">
          <header>
            <div id="sidebar_header_avatar">
              <img src="https://storageimageazure.blob.core.windows.net/paymentcontainer/logo.jpg" width="75px" />
            </div>
            <div>
              <b> {name}</b>
            </div>
            <div id="role">
              <a>{role}</a>
            </div>
            <div id="line"></div>
          </header>
          <ul className="nav">
            <li className={`${activeTab === "Bo Co Ca Hng" ? "sidebarActive" : ""}`} onClick={() => handleClick("Bo Co Ca Hng")}>

              <Link to="/manager/report">
                <i className="fas fa-store"></i><p>Bo Co Ca Hng</p>
              </Link>
            </li>
            <li onClick={() => handleClick("Qun L n Hng")}>
              <Link to="/manager/revenue">
                <i className="fas fa-dollar-sign"></i><p>Qun L n Hng</p>
              </Link>
            </li>
            <li>
              <Link to="/manager/paytype">
                <i className="fas fa-file-invoice-dollar"></i><p>Qun L Thanh Ton</p>
              </Link>
            </li>
            <li>
              <Link to="/manager/product">
                <i className="fas fa-flag"></i><p>Qun L Sn Phm</p>
              </Link>
            </li>
            <li>
              <Link to="/manager/warranty">
                <i className="fas fa-user-shield"></i><p>Qun L Phiu Bo Hnh</p>
              </Link>
            </li>
            <li>
              <Link to="/manager/customer">
                <i className="fas fa-users"></i><p>Qun L Khch Hng</p>
              </Link>
            </li>
            {/* <li>
              <Link to="/manager/updatePriceGold">
                <i className="fas fa-wrench"></i> Cp Nht Gi Vng ****
              </Link>
            </li> */}
            <li>
              <Link to="/manager/category">
                <i className="fas fa-mortar-pestle"></i><p>Qun L Danh Mc</p>
              </Link>
            </li>
            <li>
              <Link to="/manager/stone">
                <i className="far fa-gem"></i><p>Qun L </p>
              </Link>
            </li>
            <li>
              <Link to="/manager/gold">
                <i className="fas fa-coins"></i><p>Qun L Loi Vng</p>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Sidebar;
